/* eslint-disable */

import Vue from 'vue';
import Mock from 'mockjs';
import qs from 'qs';

// 
const count = 100;

// 
const queryField = {
  cName: 'string',
  userNO: 'string',
  domainId: 'string',
};

// 
const rulesField = {
  'personId': '@increment(1)',                  // Guid
  'cName': '@cname',                            // 
  'userNO': '@word(1, 2)',                      // 
  'domainId': '@cword(4, 7)',                   // 
  'quarters': '@cword(2, 5)',                   // 
  'eName': '@name',                             // 
  'birthDay': '@date("yyyy-MM-dd")',            // 
  'email': '@email',                            // 
  'sex': '@pick(["", ""])',                 // 
  'idCard': '@id',                              // 
  'nation': '@cword(2)',                        // 
  'orderNum': '@integer(1, 9)',                 // 
  'degree': '@ctitle(4, 5)',                    // 
  'highestDegree': '@ctitle(4, 5)',             // 
  'school': '@ctitle(5, 7)',                    // 
  'specialty': '@ctitle(5, 7)',                 // 
  'mobile': '@string("number", 11)',            // 
  'homeTel': '@string("number", 8)',            // 
  'birthPlace': '@county(true)',                // 
  'homeAddress': '@county(true)',               // 
  'technicalTitle': '@ctitle',                  // 
  'technicalTitleDate': '@date("yyyy-MM-dd")',  // 
  'technologyNO': '@ctitle',                    // 
  'certificateNO': '@word',                     // 
  'certificateDate': '@date("yyyy-MM-dd")',     // 
  'yearsInThePosition': '@integer(1, 9)',       // 
  'joinCompanyTime': '@date("yyyy-MM-dd")',     // 
  'workStartTime': '@date("yyyy-MM-dd")',       // 
  'leaveCompanyTime': '@date("yyyy-MM-dd")',    // 
  'domainIdArr': '@range(2, 24, 2)',            // 
  'tecCompetence': '@cparagraph(1)',            // 
  'remark': '@cparagraph',                      // 
};

/**
 *  queryString
 * @param {*} url
 */
const formatQs = (url) => {
  const search = url.split('?')[1];
  if (!search) return {};
  return JSON.parse(`{"${decodeURIComponent(search).replace(/"/g, '\\"').replace(/&/g, '","').replace(/=/g, '":"')}"}`);
};

let arr = [];
for (let i = 0; i < count; i++) {
  arr.push(Mock.mock(rulesField));
}

export default {
  // 
  queryList: (config) => {
    const qf = formatQs(config.url);
    qf.page = Number((qf.page || 1), 10);
    qf.rows = Number((qf.rows || 15), 10);
    const qfArr = Object.keys(queryField);
    const mockList = arr.filter((item) => {
      const checkArr = new Array(qfArr.length);
      qfArr.forEach((val, key) => {
        const keyType = queryField[val];
        const qfKey = qf[val];
        if (!qfKey) {
          checkArr[key] = true;
        } else if (keyType === 'string') {
          checkArr[key] = item[val].indexOf(qfKey) >= 0;
        } else if (keyType === 'date') {
          checkArr[key] = item[val] !== Vue.hp.dateFormat(new Date(qfKey), 'Y-m-d');
        } else if (keyType === 'number') {
          checkArr[key] = item[val] !== (qfKey - 0);
        }
      });
      return checkArr.every(v => v);
    });
    const start = qf.rows * (qf.page - 1);
    const end = start + qf.rows;
    const data = mockList.slice(start, end);
    return {
      total: mockList.length,
      rows: data,
    };
  },
  // 
  createItem: (config) => {
    const formItem = qs.parse(config.body);
    const rulesItem = Object.assign({}, rulesField, formItem);
    arr.unshift(Mock.mock(rulesItem));
    return { message: '' };
  },
  // 
  deleteItem: (config) => {
    const ids = Object.values(qs.parse(config.body));
    arr = arr.filter(movie => !ids.includes(movie.id));
    return {
      message: '',
    };
  },
  // 
  updateItem: (config) => {
    const formItem = qs.parse(config.body);
    const id = Object.keys(queryField)[0];
    arr.some((item) => {
      let m = item;
      if (m[id] === formItem[id]) {
        m = formItem;
        return true;
      }
      return false;
    });
    return {
      message: '',
    };
  },
};



