// 
export const routesMap = [
  {
    path: '/',
    name: 'index',
    redirect: '/index',
    meta: { role: false, title: '', icon: 's fa-home' },
    component: DefaultLayout,
    children: [
      {
        path: '/index',
        component: lazy('Home'),
      },
    ],
  },
  {
    path: '/login',
    name: 'login',
    meta: { role: false },
    component: lazy('Login'),
    hidden: true,
  },
  {
    path: '/404',
    name: 'NotFound',
    meta: { role: false },
    component: lazy('404'),
    hidden: true,
  },
];

const router = new Router({
  // mode: 'history',
  scrollBehavior: () => ({ y: 0 }),
  base: process.env.BASE_URL,
  routes: routesMap,
});

// 
router.beforeEach((to, form, next) => {
  // 
  if (store.state.isSaveTags.length) {
    if (store.state.isSaveTags.find(v => v === form.name)) {
      MessageBox.confirm(', , ?', '').then(() => {
        store.commit('RemoveIsSaveTags', form.name);
        next();
      }).catch(() => {
        next(false);
      });
      return;
    }
  }
  // token
  if (to.name !== 'login' && !store.state.auth.token) {
    next('login');
    return;
  }
  // 
  if (to.meta && !to.meta.role) {
    next();
  }
  // 
  if (!store.state.auth.roles.length && store.state.auth.token) {
    // 
    store.dispatch('auth/GetInfo').then(() => {
      // 
      store.dispatch('auth/CreateRoutes').then(() => {
        // 
        router.addRoutes(store.state.auth.addRouters);
        // hack addRoutes
        next({ ...to, replace: true });
      });
    });
  }
  next();
});

export default router;



